.auth__form {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.auth__form button i {
    vertical-align: middle;
    margin-left: 0.2rem;
}
.form__fields {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.auth__form > button:hover {
    box-shadow: 0 15px 30px rgba(20, 140, 252, 0.7) !important;
    transition: .5s;
}
.auth__form .form__fields label > input {
    margin-top: 1rem;
}
.auth__form .form__fields label:not(:last-child) {
    display: inline-block;
    margin-bottom: 1rem;
    font-weight: 400;
}
#create_account {
    text-align: center;
}

.email_icon {
    background-image: url("../../assets/img/icons/email_icon.svg");
    background-repeat: no-repeat;
    background-size: 2rem 2rem;
    background-position: 0.5rem 50%;
}
.password_icon {
    background-image: url("../../assets/img/icons/passowrd_icon.svg");
    background-repeat: no-repeat;
    background-size: 2rem 2rem;
    background-position: 0.5rem 50%;
}